import React, { useState, useEffect, useRef } from "react";
import { Link, NavLink, useLocation } from "react-router-dom";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import light from "./Photos/light.png";
import img from "./Photos/logo.png";
import "./Navbar.css";

const Navbar = () => {
  const [activeNavItem, setActiveNavItem] = useState("");
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const sidebarRef = useRef(null);
  const [isLightVisible, setIsLightVisible] = useState(true);
  const location = useLocation();

  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY;
      const shouldShowLight = scrollY === 0;
      setIsLightVisible(shouldShowLight);
    };

    const handleTouchStart = (event) => {
      const target = event.target;
      if (
        sidebarRef.current &&
        !sidebarRef.current.contains(target) &&
        sidebarOpen
      ) {
        setSidebarOpen(false);
      }
    };

    window.addEventListener("scroll", handleScroll);
    document.addEventListener("touchstart", handleTouchStart);

    return () => {
      window.removeEventListener("scroll", handleScroll);
      document.removeEventListener("touchstart", handleTouchStart);
    };
  }, [sidebarOpen]);

  useEffect(() => {
    // Update activeNavItem based on the current location
    const path = location.pathname;
    if (path === "/") {
      setActiveNavItem("home");
    } else if (path === "/services") {
      setActiveNavItem("services");
    } else if (path === "/about") {
      setActiveNavItem("about");
    }
  }, [location]);

  const handleNavItemClick = (item) => {
    setActiveNavItem(item);
    setSidebarOpen(false);  // Close the sidebar when a navigation item is clicked
  };

  const handleSidebarToggle = () => {
    setSidebarOpen(!sidebarOpen);
  };

  return (
    <div>
      <nav className={`navbar navbar-expand-lg ${sidebarOpen ? "open" : ""}`}>
        <div className="container">
          <Link to="/" className="navbar-brand">
            {/* <img src={img} alt="Logo" className="logo" /> */}
            <svg
              className="logo"
              xmlns="http://www.w3.org/2000/svg"
              width="157"
              height="49"
              viewBox="0 0 157 49"
              fill="none"
            >
              <path
                d="M24.9567 28.6685C27.8479 26.4926 29.6629 23.3852 30.6696 20.5253C31.7203 17.5405 29.9503 14.5368 27.1842 13L9.35125 2.94207C7.3942 1.83828 4.91957 2.391 3.61816 4.2226C2.63093 5.61203 2.53754 7.44743 3.37864 8.92989L11.5554 23.3416C14.4137 28.3796 20.3286 32.1516 24.9567 28.6685Z"
                fill="#FAF862"
                stroke="#0A0B1D"
                stroke-width="4"
              />
              <path
                d="M38.5763 42.4426C36.4506 42.4426 34.026 41.9508 32 40.8361L33.6607 37.4262C35.1885 38.3115 36.9488 39 38.8088 39C40.0709 39 40.7019 38.6721 40.7019 37.918C40.7019 37.3279 40.2702 37.0328 39.3734 36.6066L36.6167 35.4262C34.1921 34.3771 33.1625 32.8689 33.1625 30.9016C33.1625 27.9508 35.2549 26.3115 39.1077 26.3115C40.868 26.3115 42.7612 26.7049 44.5547 27.3607L43.1929 30.7377C41.798 30.1475 40.2702 29.6885 38.8752 29.6885C37.6795 29.6885 37.3142 30.0492 37.3142 30.6066C37.3142 31.1967 37.7127 31.5246 38.7756 31.9836L41.5323 33.1639C43.6911 34.0492 44.92 35.3279 44.92 37.5902C44.92 40.9344 42.3958 42.4426 38.5763 42.4426Z"
                fill="#0A0B1D"
              />
              <path
                d="M51.296 48.5082H47.244V26.8033H51.296V28.9344H51.3624C52.3256 27.3607 53.9863 26.3115 56.4773 26.3115C60.4961 26.3115 63.8507 29.1967 63.8507 34.3771C63.8507 39.5574 60.4961 42.4426 56.4773 42.4426C53.9863 42.4426 52.3256 41.3934 51.3624 39.8197H51.296V48.5082ZM55.4477 38.6393C57.9055 38.6393 59.5994 36.9344 59.5994 34.3771C59.5994 31.8197 57.9055 30.1148 55.4477 30.1148C52.9899 30.1148 51.296 31.8197 51.296 34.3771C51.296 36.9344 52.9899 38.6393 55.4477 38.6393Z"
                fill="#0A0B1D"
              />
              <path
                d="M74.083 26.3115C78.9654 26.3115 82.5857 29.623 82.5857 34.3771C82.5857 39.1311 78.9654 42.4426 74.083 42.4426C69.2007 42.4426 65.5804 39.1311 65.5804 34.3771C65.5804 29.623 69.2007 26.3115 74.083 26.3115ZM74.083 30.1148C71.592 30.1148 69.7653 31.7869 69.7653 34.3771C69.7653 36.9672 71.592 38.6393 74.083 38.6393C76.574 38.6393 78.3676 36.9672 78.3676 34.3771C78.3676 31.7869 76.574 30.1148 74.083 30.1148Z"
                fill="#FFFD7C"
              />
              <path
                d="M93.2786 26.8033V30.1803H90.1233V36.541C90.1233 37.7541 90.5551 38.2459 91.7508 38.2459H92.4483V41.9836H90.6548C87.7652 41.9836 86.0381 40.541 86.0381 37.4918V30.1803H83.2149V26.8033H86.0381V20.7377H90.1233V26.8033H93.2786Z"
                fill="#0A0B1D"
              />
              <path
                d="M95.7359 19H99.8212V41.9508H95.7359V19Z"
                fill="#0A0B1D"
              />
              <path
                d="M103.319 19.1967H107.969V23.623H103.319V19.1967ZM107.67 41.9508H103.585V26.8033H107.67V41.9508Z"
                fill="#0A0B1D"
              />
              <path
                d="M119.339 49C117.612 49 115.32 48.7049 113.095 48.2787L113.826 44.4426C116.051 44.9672 117.878 45.1967 119.173 45.1967C121.897 45.1967 122.993 44.2459 122.993 41.2295V39.8197H122.926C121.963 41.2295 120.369 42.3443 117.778 42.3443C113.759 42.3443 110.438 39.4918 110.438 34.3771C110.438 29.1967 113.793 26.3115 117.811 26.3115C120.302 26.3115 121.963 27.3607 122.926 28.9344H122.993V26.8033H127.045V41.9508C126.978 47.3279 123.923 49 119.339 49ZM118.841 38.6393C121.299 38.6393 122.993 36.9344 122.993 34.3771C122.993 31.8197 121.299 30.1148 118.841 30.1148C116.383 30.1148 114.689 31.8197 114.689 34.3771C114.689 36.9344 116.383 38.6393 118.841 38.6393Z"
                fill="#0A0B1D"
              />
              <path
                d="M130.636 19H134.721V28.9672H134.788C135.851 27.2951 137.511 26.3115 139.77 26.3115C143.091 26.3115 145.549 28.1475 145.549 32.6066V41.9508H141.464V33.5574C141.464 31.0984 140.301 30.0492 138.275 30.0492C136.216 30.0492 134.721 31.2623 134.721 34.541V41.9508H130.636V19Z"
                fill="#0A0B1D"
              />
              <path
                d="M156.884 26.8033V30.1803H153.728V36.541C153.728 37.7541 154.16 38.2459 155.356 38.2459H156.053V41.9836H154.26C151.37 41.9836 149.643 40.541 149.643 37.4918V30.1803H146.82V26.8033H149.643V20.7377H153.728V26.8033H156.884Z"
                fill="#0A0B1D"
              />
              <path
                d="M74.2093 31.5581C76.0121 31.5581 77.3488 32.7039 77.3488 34.3488C77.3488 35.9938 76.0121 37.1395 74.2093 37.1395C72.4065 37.1395 71.0698 35.9938 71.0698 34.3488C71.0698 32.7039 72.4065 31.5581 74.2093 31.5581Z"
                fill="#0A0B1D"
              />
            </svg>
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            onClick={handleSidebarToggle}
          >
            <div className="dots"></div>
            <div className="dots"></div>
            <div className="dots"></div>
          </button>
          <div
            ref={sidebarRef}
            className={`collapse navbar-collapse ${sidebarOpen ? "show" : ""}`}
            id="navbarNav"
          >
            <ul className="navbar-nav ms-auto">
              <li
                className={`nav-item ${
                  activeNavItem === "home" ? "active" : ""
                }`}
              >
                <NavLink to="/" className="nav-link" activeClassName="active" onClick={() => handleNavItemClick("home")} draggable="false" >
                  Home
                </NavLink>
              </li>
              <li
                className={`nav-item ${
                  activeNavItem === "services" ? "active" : ""
                }`}
              >
                <NavLink
                  to="/services"
                  className="nav-link"
                  activeClassName="active"
                  onClick={() => handleNavItemClick("services")} draggable="false"
                >
                  Services
                </NavLink>
              </li>
              <li
                className={`nav-item ${
                  activeNavItem === "about" ? "active" : ""
                }`}
              >
                <NavLink
                  to="/about"
                  className="nav-link"
                  activeClassName="active"
                  onClick={() => handleNavItemClick("about")} draggable="false"
                >
                  About
                </NavLink>
              </li>
              <li className="nav-item">
                <Link to="/contact" className="nav-link contact"   onClick={() => handleNavItemClick("contact")} draggable="false">
                  Contact
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <img src={light} alt="Logo 2" className="light" draggable="false" />
    </div>
  );
};

export default Navbar;

